.up{
  -webkit-transform: translate(50%, 0);
  transform: translate(50%, 0);
  -webkit-animation: Down_anim 2s ease-in-out infinite;
  animation: Down_anim 2s ease-in-out infinite;
}
.down{
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  -webkit-animation: Down_anim 2s ease-in-out infinite;
  animation: Down_anim 2s ease-in-out infinite;
}
@-webkit-keyframes Down_anim {
0%, 100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
}
50% {
    -webkit-transform: translate(0, 20px);
    transform: translate(0, 20px);
}
}
@keyframes Down_anim {
0%,100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
}
50% {
    -webkit-transform: translate(0, 20px);
    transform: translate(0, 20px);
}
}
@-webkit-keyframes up_anim {
0%, 100% {
    -webkit-transform: translate(0, 20px);
    transform: translate(0, 20px);
}
50% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
}
}
@keyframes up_anim {
0%,100% {
    -webkit-transform: translate(0, 20px);
    transform: translate(0, 20px);
}
50% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
}
}

